.imageGeek {
  width: 100px;
  height: 85px;
}

.container-text-certi {
  margin-left: 4%;
  text-align: center;
}

.container-text-certi > h3 {
  margin-bottom: 5%;
  margin-top: 2%;
  color: #5b43d6;
  font-size: 15px;
  text-transform: uppercase;
  font-weight: 600;
  line-height: 1.6;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.container-col {
  padding: 10% 5%;
  border-radius: 15px;
  width: 500px;
  height: 120px;
  margin-top: 5%;
}

.container-text-certi > a {
  text-decoration: none;
}

.containerImage {
  border-radius: 500px;
}
.geek {
  transition: all ease-out 0.2s;
}
.geek:hover {
  border: 1px solid #5b43d6, #2e2849;
  border-radius: 15px;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.12);
  transform: scale(1.05);
  padding-bottom: 5%;
  cursor: pointer;
}

.col-freecode {
  margin-top: 20%;
}
.geek {
  padding: 5% 0;
}

.text-resumen {
  color: #212529;
  line-height: 1.6;
  margin-bottom: 30px;
}

.title-main {
  margin-top: 1%;
}
.title-certification {
  margin-bottom: 0 !important;
}

@media only screen and (max-width: 540px) {
  .row-certifications {
    flex-wrap: wrap;
    display: block !important;
  }

  .col-certification {
    margin-top: 20%;
  }
  .col-freecode {
    margin-top: 0%;
  }
  .container-col {
    width: 100%;
    margin: auto;
    margin-bottom: 70%;
  }
  .row-frecode {
    display: none;
  }
  .container-camp {
    margin-bottom: 30%;
  }
  .container-camp-2 {
    margin-bottom: 42%;
  }
}

@media only screen and (min-width: 768px) and (max-width: 912px) {
  .row-certifications {
    flex-wrap: wrap;
    display: block !important;
  }

  .col-certification {
    margin-top: 10%;
  }
  .col-freecode {
    margin-top: 0%;
  }
  .container-col {
    width: 100%;
    margin: auto;
    margin-bottom: 25%;
  }
  .row-frecode {
    display: none;
  }
  .container-camp {
    margin-bottom: 17%;
  }
  .container-camp-2 {
    margin-bottom: 22%;
  }
}

@media only screen and (min-width: 280px) and (max-width: 280px) {

  .container-col {
    margin-bottom: 100%;
  }
  .container-camp {
    margin-bottom: 28%;
  }
  .container-camp-2 {
    margin-bottom: 45%;
  }
}